import React, { Component, useState } from "react";
import { Nav, OverlayTrigger, Tooltip } from "react-bootstrap";
import Main from "./Main";
import Participation from "./Participation";
import MyInfo from "./MyInfo";
import Excution from "./Excution";
import Plan from "./Plan";
import RegisterManager from "./RegisterManager";
import ResultManager from "./ResultManager";

function MainNav(props) {
  let [tab, tabMod] = useState("link-0");
  let manager = props.manager;
  return (
    <div className="container" style={{ paddingTop: "20px" }}>
      {props.manager ? (
        <Nav fill variant="tabs" defaultActiveKey="link-0" onSelect={tabMod}>
          <Nav.Item>
            <Nav.Link eventKey="link-0">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    Home
                  </Tooltip>
                }
              >
                {/* <b style={{ fontSize: "1.5em" }}>HOME</b> */}
                <i class="fa fa-home fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="m_link-1">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    등록
                  </Tooltip>
                }
              >
                <i class="fa fa-pencil-square-o fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="m_link-2">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    결과
                  </Tooltip>
                }
              >
                {/* <i class="fa fa-calendar-plus-o fa-3x" aria-hidden="true"></i> */}
                <i class="fa fa-check-square-o  fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
        </Nav>
      ) : (
        <Nav fill variant="tabs" defaultActiveKey="link-0" onSelect={tabMod}>
          <Nav.Item>
            <Nav.Link eventKey="link-0">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    Home
                  </Tooltip>
                }
              >
                {/* <b style={{ fontSize: "1.5em" }}>HOME</b> */}
                <i class="fa fa-home fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    참여
                  </Tooltip>
                }
              >
                <i class="fa fa-hand-paper-o fa-3x" aria-hidden="true"></i>
                {/* <i class="fa fa-handshake-o fa-3x" aria-hidden="true"></i> */}
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-2">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    계획
                  </Tooltip>
                }
              >
                <i class="fa fa-calendar-plus-o fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-3">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    실행등록
                  </Tooltip>
                }
              >
                <i class="fa fa-calendar-check-o  fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-4">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={
                  <Tooltip id="button-tooltip" style={{ fontSize: "1.3em" }}>
                    내정보
                  </Tooltip>
                }
              >
                <i class="fa fa-user-circle-o fa-3x" aria-hidden="true"></i>
              </OverlayTrigger>
            </Nav.Link>
          </Nav.Item>
        </Nav>
      )}

      {tab === "link-0" ? <Main manager={manager}></Main> : null}
      {tab === "link-1" ? <Participation></Participation> : null}
      {tab === "link-2" ? <Plan></Plan> : null}
      {tab === "link-3" ? <Excution></Excution> : null}
      {tab === "link-4" ? <MyInfo></MyInfo> : null}
      {tab === "m_link-1" ? <RegisterManager></RegisterManager> : null}
      {tab === "m_link-2" ? <ResultManager></ResultManager> : null}
    </div>
  );
}

function selectItem(eventKey) {
  console.log(eventKey);
}
export default MainNav;
