import React, { Component } from "react";
import { Card, Button } from "react-bootstrap";

function Main(props) {
  let goalUser = "홍길동";
  let goalName = "자격증 따기";
  return (
    <div>
      <div className="container text-left" style={{ paddingTop: "20px" }}>
        {props.manager ? (
          <Card body style={{ fontSize: "1.5em", border: "none" }}>
            {goalUser}님의 {goalName} 결과가 등록되었습니다.
            <span style={{ paddingLeft: "15px", color: "blue" }}>
              [결과 확인]
            </span>
          </Card>
        ) : (
          <Card body style={{ fontSize: "1.5em", border: "none" }}>
            {goalName}가 종료되었습니다.
            <span style={{ paddingLeft: "15px", color: "blue" }}>
              [결과 등록]
            </span>
          </Card>
        )}

        <Card>
          <Card.Body>
            <Card.Title>Card Title</Card.Title>
            <Card.Text>
              Some quick example text to build on the card title and make up the
              bulk of the card's content.
            </Card.Text>
          </Card.Body>

          <Card.Img style={{ height: "100px", backgroundColor: "#333" }} />
        </Card>
        <Card>
          <Card.Body>
            <Card.Title>Card Title</Card.Title>
            <Card.Text>
              Some quick example text to build on the card title and make up the
              bulk of the card's content.
            </Card.Text>
          </Card.Body>

          <Card.Img style={{ height: "100px", backgroundColor: "#333" }} />
        </Card>
      </div>
    </div>
  );
}

export default Main;
